import axios from "axios";
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import { deleteOtp, getToken, removeEmail, setEmail, setOTP, setToken, setUserDetails } from "../helper/SessionHelper";
import { SetProfile } from "../redux/state-slice/profileSlice";
import { Hideloader, Showloader } from "../redux/state-slice/settingsSlice";
import { SetSummary } from "../redux/state-slice/summarySlice";
import { SetCanceledTask, SetCompletedTask, SetNewTask, SetProgressTask } from "../redux/state-slice/taskSlice";
import store from "../redux/store/store";

const BaseUrl = "https://todotaskmanager-shahid.herokuapp.com";
const AxiosHeader={headers:{'token':getToken()}}

export function RegistrationRequest(
  email,
  firstName,
  lastName,
  mobile,
  password
) {
  //call start
  store.dispatch(Showloader());
  let URL = BaseUrl + "/register";
  let PostBody = {
    email: email,
    firstName: firstName,
    lastName: lastName,
    mobile: mobile,
    password: password,
    photo:"data:image/webp;base64,"
  };

  return axios
    .post(URL, PostBody)
    .then((res) => {
            store.dispatch(Hideloader());
        if (res.status === 200) {
            
            if(res.data.status==='fail'){
                
                if(res.data.data.keyPattern.email===1){

                    toast.warn(' Email Already Exist', {
                        position: "bottom-center",
                        autoClose: 4000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                        theme: "light",
                        });
                    return false
                    
                }
           
            }else{
                toast.success('Registration Successfull!', {
                    position: "bottom-center",
                    autoClose: 5000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                    theme: "light",
                    });
                return true
            } 
        }else{
            return false
        }
    })
    .catch((err) => {
      //call end
      store.dispatch(Hideloader());   
      return false;
    });
}
export function LoginRequest(email,password) {
  //call start
  store.dispatch(Showloader());
  let URL = BaseUrl + "/login";
  let PostBody = {email: email,password: password};

  return axios
    .post(URL, PostBody)
    .then((res) => {
            store.dispatch(Hideloader());
        if (res.status === 200) {
            
            setToken(res.data.token);
            setUserDetails(res.data.data) 
            toast.success('Login Successfull!', {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
            return true
        }else{
            toast.error('Email or Password Not Match Api',{
                position: "bottom-center"})
            return false
        }
    })
    .catch((err) => {
      //call end
      store.dispatch(Hideloader());   
      return false;
    });
}


export function CreateTaskRequest(
    title, description) {
    //call start
    store.dispatch(Showloader());
    let URL = BaseUrl + "/createTask";
    let PostBody = {
        title: title,
        description: description,
        status: "New"
    };
  
    return axios
      .post(URL, PostBody,AxiosHeader)
      .then((res) => {
              store.dispatch(Hideloader());
          if (res.status === 200) {
            toast.success('Task create Successfull!', {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                return true
              
          }else{
            toast.error('Task Not Create. Someting wrong!',{
                position: "bottom-center"})
              return false
          }
      })
      .catch((err) => {
        //call end
        store.dispatch(Hideloader());   
        return false;
      });
  }

  export function SelectByStatus(Status){
    let URL=BaseUrl+"/selectByStatus/"+Status;
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            if(Status==="New"){
                store.dispatch(SetNewTask(res.data.data))
            }else if(Status==="Completed"){
                store.dispatch(SetCompletedTask(res.data.data))
            }else if(Status==="Canceled"){
                store.dispatch(SetCanceledTask(res.data.data))
            }else if(Status==="Progress"){
                store.dispatch(SetProgressTask(res.data.data))
            }else{
                toast.error(' Someting wrong!',{
                    position: "bottom-center"})  
            }
        }else{
            
        }
    })
  }
  export function SummaryRequest(){
    let URL=BaseUrl+"/taskStatusCount"
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            store.dispatch(SetSummary(res.data.data))
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }
  export function TaskDeleteRequest(id){
    let URL=BaseUrl+"/deleteTask/"+id
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            toast.success(' Item Deleted',{
                position: "bottom-center"})
            return true
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }
  export function UpdateStatusRequest(id,Status){
    let URL=BaseUrl+"/updateStatus/"+id +"/"+Status
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            toast.success(' Status Change Successfull',{
                position: "bottom-center"})
            return true
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }

  export function getProfileData(){
    let URL=BaseUrl+"/getProfile"
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            
            store.dispatch(SetProfile(res.data.data[0]))
            return true
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }

  export function ProfileUpdateRequest(email,firstName,lastName,mobile,password,photo) {
    //call start
    store.dispatch(Showloader());
    let URL = BaseUrl + "/profileUpdate";
    let PostBody = {
      email:email,
      firstName: firstName,
      lastName: lastName,
      mobile: mobile,
      password: password,
      photo:photo
    };
    let UserDetails={
        email:email,
        firstName: firstName,
        lastName: lastName,
        mobile: mobile,
        photo:photo
      };
  
    return axios
      .post(URL, PostBody,AxiosHeader)
      .then((res) => {
              store.dispatch(Hideloader());
          if (res.status === 200) {
            toast.success('Profile Update Successfull!', {
                position: "bottom-center",
                autoClose: 5000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
                theme: "light",
                });
                setUserDetails(UserDetails) 
            return true
           
          }else{
              return false
          }
      })
      .catch((err) => {
        //call end
        store.dispatch(Hideloader());   
        return false;
      });
  }

  export function RecoveryEmailRequest(email){
    let URL=BaseUrl+"/recoveryEmail/"+email
    store.dispatch(Showloader());
    return axios.get(URL).then(res=>{
        store.dispatch(Hideloader());
        
        if(res.data.status!=='fail'){
            toast.success(' OTP send ',{
                position: "bottom-center"})
            
            setEmail(res.data.data.accepted[0])
            return true
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }
  export function RecoveryVerifyOTPRequest(email,OTP){
    let URL=BaseUrl+"/verifyOTP/" + email + "/" + OTP
    store.dispatch(Showloader());
    return axios.get(URL).then(res=>{
        store.dispatch(Hideloader());
        
        if(res.status===200 && res.data.status==="success"){
            
            toast.success(' OTP Verified',{
                position: "bottom-center"})
                setOTP(OTP)
            return true
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }

  export function CreatePasswordRequest(
    email,OTP, password
  ) {
    //call start
    store.dispatch(Showloader());
    let URL = BaseUrl + "/passwordUpdate";
    let PostBody = {
      email:email,otp:OTP, password: password,
     
    };
  
    return axios
      .post(URL, PostBody)
      .then((res) => {
              store.dispatch(Hideloader());
              console.log(res)
          if (res.status === 200 && res.data.status==="success") {
            toast.success(' Password Change Successfull',{
                position: "bottom-center"})
                deleteOtp()
                removeEmail()
         return true
          }else{
              return false
          }
      })
      .catch((err) => {
        //call end
        store.dispatch(Hideloader());   
        return false;
      });
  }

  export function getSearchData(searchKey,status){
    let URL=BaseUrl+"/search/"+status+"/"+searchKey
    store.dispatch(Showloader());
    return axios.get(URL,AxiosHeader).then(res=>{
        store.dispatch(Hideloader());
        if(res.status===200){
            
           
            return res.data.data
        }else{
            toast.error(' Someting wrong!',{
                position: "bottom-center"}) 
        }
    })
  }
